<template>
  <div class="my-modal" @click="close('register')">
    <div class="my-modal-content" @click.stop>
      <h2>Регистрация</h2>

      <form @submit.prevent="register">
        <div class="my-modal-rows">
          <div class="my-modal-row">
            <input type="text" id="fullName" v-model="first_name" required placeholder="ФИО"/>
          </div>

          <div class="my-modal-row">
            <input type="text" id="address" v-model="address" placeholder="Адрес"/>
          </div>

          <div class="my-modal-row">
            <input type="tel" id="phoneNumber" v-model="phone" required placeholder="Номер телефона"/>
          </div>

          <div class="my-modal-row">
            <input type="email" id="email" v-model="email" required placeholder="Почта"/>
          </div>

          <div class="my-modal-row">
            <input
                :type="[showPassword ? 'text' : 'password']"
                id="password"
                v-model="password"
                required
                placeholder="Пароль"
            />
            <span class="password-toggle" @click="togglePasswordVisibility">

            </span>
          </div>

          <div class="my-modal-row">
            <input
                :type="[showPassword ? 'text' : 'password']"
                id="confirmPassword"
                v-model="confirmPassword"
                required
                placeholder="Повторите пароль"
            />
            <span class="password-toggle" @click="togglePasswordVisibility">

            </span>
          </div>
        </div>

        <div class="check-box">
          <label>
            <span @click="checkAuth" :class="[send_type === 2 ? 'check' : '']"></span>
            <span>Верификация по почте</span>
          </label>
          <label>
            <span @click="checkAuth" :class="[send_type === 1 ? 'check' : '']" style="pointer-events: none"></span>
            <span>Верификация по телефону номера</span>
          </label>

          <label>
            <span @click="checkTerms" :class="[acceptTerms ? 'check' : '']"></span>
            <span>Принимаю условия  пользовательского соглашения</span>
          </label>

          <label>
            <span @click="checkPrivacy" :class="[acceptPrivacy ? 'check' : '']"></span>
            <span>Принимаю условия  политики возврата денежных средств</span>
          </label>
        </div>

        <div class="btn-block">
          <button class="custom-button" type="submit" :disabled="!(acceptTerms && acceptPrivacy && password && (password === confirmPassword))">Регистрация</button>
        </div>
      </form>

      <div class="another_links">
        <p>У вас есть профиль? <span @click="[openLogin(), close('register')]">Войти</span></p>
        <p>Забыли пароль? <span @click="[openModalPasswordRecovery(), close('register')]">Восстановить</span></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      first_name: '',
      address: '',
      phone: '',
      email: '',
      password: '',
      confirmPassword: '',
      acceptTerms: false,
      acceptPrivacy: false,
      showPassword: false,
      send_type: 2
    };
  },
  props: ['close', 'openLogin', 'openModalPasswordRecovery'],
  methods: {
    register() {

      const data = {
        first_name: this.first_name,
        email: this.email,
        phone: this.phone,
        address: this.address,
        send_type: this.send_type,
        password: this.password,
        country: '',
        city: '',
      }
      this.$store.dispatch('postUser', data)
    },
    togglePasswordVisibility() {
      this.showPassword = !this.showPassword;
    },
    checkTerms() {
      this.acceptTerms = !this.acceptTerms
    },
    checkPrivacy() {
      this.acceptPrivacy = !this.acceptPrivacy
    },
    checkAuth() {
      this.send_type = this.send_type === 1 ? 2 : 1
    }
  }
};
</script>

