  line-height: 22px;
  color: #7B7979;
}

.check-box label span:first-child {
  display: inline-block;
  position: relative;
  width: 24px;
  height: 24px;
  background: rgba(30, 30, 30, 0.1);
  border-radius: 2px;
  cursor: pointer;
}

