<template>
    <Form v-on:submit="sendContactForm" :validation-schema="validate" ref="form" class="left-bar-wrap feed-back-form" autocomplete="off">
        <div class="d-flex justify-content-center border-bottom pb-4 mb-4">
            <img src="../assets/img/auction-text.svg">
        </div>
        <div class="form-group">
            <label class="search-label">Имя</label>
            <Field type="text" name="fullname" class="search-input" v-model="fullname" placeholder="Введите имя" />
            <div class="error-input-wrap">
                <ErrorMessage class="error-wrap" name="fullname" />
            </div>
        </div>
        <div class="form-group">
            <label class="search-label">Телефон</label>
            <Field type="text" name="phone" class="search-input" v-model="phone" v-mask="phoneMask" placeholder="Введите номер телефона" />
            <div class="error-input-wrap">
                <ErrorMessage class="error-wrap" name="phone" />
            </div>
        </div>
        <div class="form-group">
            <label class="search-label">Тип авто</label>
            <select class="search-select" v-model="vehicle">
                <option v-for="vehicle in vehicles" :key="vehicle" :value="vehicle.id">{{ vehicle.title_ru ? vehicle.title_ru : vehicle.title }}</option>
            </select>
        </div>
        <div class="form-group">
            <label class="search-label">Марка</label>
            <select class="search-select" v-model="brand">
                <option value="0">Все марки</option>
                <option v-for="brand in brands" :key="brand" :value="brand.id">{{ brand.title }}</option>
            </select>
        </div>
        <div class="form-group border-bottom pb-4">
            <label class="search-label">Модель</label>
            <select class="search-select" v-model="model">
                <option value="0">Все модели</option>
                <option v-for="model in models" :key="model" :value="model.id">{{ model.title }}</option>
            </select>
        </div>
        <div class="d-flex justify-content-between mt-4">
            <label class="search-label">Цена от</label>
            <label class="search-label">Цена до</label>
        </div>
        <div class="border-bottom mb-3">
            <Slider :min="min" :max="max" :merge="merge" :format="format" v-model="value" class="slider"></Slider>
        </div>
        <div class="d-flex justify-content-center">
            <button type="submit" class="custom-button">Заказать звонок</button>
        </div>
    </Form>
</template>

<script>
import Slider from '@vueform/slider'
import { Form, Field, ErrorMessage } from 'vee-validate'
import * as Yup from 'yup'

export default {
    data() {
        return {
            min: 0,
            max: 100000,
            value: [this.$route.query['priceFrom'] ? this.$route.query['priceFrom'] : 0, this.$route.query['priceTo'] ? this.$route.query['priceTo'] : 100000],
            merge: 0,
            format: {
                prefix: '$',
                decimals: 0
            },
            fullname: '',
            phone: '',
            phoneMask: '996#########',
            vehicle: this.$route.query['vehicle_type'] || 1,
            brand: this.$route.query['brand'],
            model: this.$route.query['model'],
        }
    },
    components: {
        Slider,
        Form,
        Field,
        ErrorMessage
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        validate() {
            return Yup.object({
                fullname: Yup.string().required('Поле обязательное для заполнения'),
                phone: Yup.string().required('Поле обязательное для заполнения')
            })
        },
        vehicles() {
            return (this.$store.getters.getVehicleList)
            ? this.$store.getters.getVehicleList
            : this.$store.dispatch('getVehicleList')
        },
        brands() {
            return this.$store.getters.getBrandList
        },
        models() {
            return this.$store.getters.getModelList
        }
    },
    watch: {
        value() {
            this.priceFrom = this.value[0]
            this.priceTo = this.value[1]
        },
        // vehicle() {
        //     if(this.vehicle && this.vehicle !== undefined) {
        //         this.brand = ''
        //         this.$store.dispatch('getBrandList', this.vehicle)
        //         this.brand = this.$route.query['brand'] ? this.$route.query['brand'] : ''
        //     }
        // },
        // brand() {
        //     if(this.brand && this.brand !== undefined) {
        //         this.model = ''
        //         this.$store.dispatch('getModelList', this.brand)
        //         this.model = this.$route.query['model'] ? this.$route.query['model'] : ''
        //     }
        // }
    },
    methods: {
        sendContactForm() {
            if(this.$refs.form.validate()) {
                var data = {
                    fullname: this.fullname,
                    phone: this.phone,
                    brand: this.brand ? this.brand : '',
                    model: this.model ? this.model : '',
                    'min_price': this.priceFrom ? this.priceFrom : '',
                    'max_price': this.priceTo ? this.priceTo : ''
                }
                this.$store.dispatch('feedBackForm', data)
                this.fullname = ''
                this.phone = ''
            }
        }
    }
}
</script>